<template>
<div id="employee-form">
    <form @submit.prevent="handleSubmit">

        <div class="input-wrapper">
            <label class="name">Employee Name</label> <br />
            <input ref="first" type="text" :class="{ 'has-error': submitting && isNameValid }" v-model="employee.name" @focus="clearStatus" @keypress="clearStatus" />
        </div>

        <div class="input-wrapper">
            <label>Employee Email</label> <br />
            <input type="text" :class="{ 'has-error': submitting && isEmailValid }" v-model="employee.email" @focus="clearStatus" />
        </div>

        <p v-if="error && submitting" class="error-message">
            ❗Please fill out all required fields and valid email address
        </p>
        <p v-if="success" class="success-message">
            ✅ Employee successfully added
        </p>
        <button class="addemployee"><img width="22" height="22" src="https://img.icons8.com/ios-glyphs/30/000000/add--v1.png" alt="add--v1" title="Add Employee"/></button>
    </form>
</div>

</template>

<script>
export default {
    name: 'employee-form',
    data() {
        return {
            submitting: false,
            error: false,
            success: false,
            employee: {
                name: '',
                email: '',
                reg: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
            },
        }
    },
    methods: {
        handleSubmit() {
            this.submitting = true
            this.clearStatus()

            if (this.isNameValid || this.isEmailValid) {
                this.error = true
                return
            }

            this.$emit('add:employee', this.employee)
            this.$refs.first.focus()
            this.employee = {
                name: '',
                email: '',
                reg: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
            }
            this.error = false
            this.success = true
            this.submitting = false
        },

        clearStatus() {
            this.success = false
            this.error = false
        },
    },
    computed: {
        isNameValid() {
            return this.employee.name === '';
        },
        isEmailValid() {
            return (this.employee.email === '') ? true : (this.employee.reg.test(this.employee.email)) ? false : true;
        }
    },
}
</script>

<style scoped>


form {

    display: flex;
    align-items: end;
    position: relative;
    gap: 5px
}

[class*='-message'] {
    font-weight: 500;
}

.error-message {
    color: #8b1d20e0;
    position: absolute;
    left: 0;
    bottom: -20px;
    margin: 0;
    font-size: 13px;
    font-weight: 500;
}

.success-message {
    color: #50C878;
    position: absolute;
    left: 0;
    bottom: -20px;
    margin: 0;
    font-size: 13px;
    font-weight: 600;
}

.input-wrapper {
    text-align: left;
    font-size: 15px;
    font-weight: 600;
}
input {

    height: 20px;
    /* border: 1px solid #fff; */
    border-radius: 5px;
    font-size: 14px;
    font-weight: 500;
    color: #000;
    border: none;
    font-family: "Varela Round", sans-serif;
    padding: 2px 8px ;
}

.input-wrapper label {
    color: #fff;
    line-height: 24px;
}
</style>
