<template>
<div class="box">

    <form action="/list" method="POST" class="form login" novalidate="true" v-on:submit.prevent="validate" autocomplete="off" id="Form">
        <div class="form__field">

            <input type="text" name="username" placeholder="Username" id="username" class="form-control" autocomplete="false" v-bind:class="{
              'is-valid': validation.valid.username,
              'is-invalid': validation.invalid.username,
            }" v-model="username" required />
        </div>

        <span class="invalid-feedback" v-if="validation.invalid.username">
            {{ validation.invalid.username }}
        </span>
        <div class="form__field">

            <input id="password" type="password" name="password" class="form__input" placeholder="Password" v-bind:class="{
              'is-valid': validation.valid.password,
              'is-invalid': validation.invalid.password,
            }" v-model="password" required />
        </div>

        <span class="invalid-feedback" v-if="validation.invalid.password">
            {{ validation.invalid.password }}
        </span>
        <div class="form__field btn-wrapper">
            <input class="btn" type="submit" value="Sign In" />
        </div>
    </form>

</div>
</template>

<script>
export default {
    data() {
        return {
            username: "",
            password: "",

            validation: {
                invalid: {},
                valid: {},
            },
        };
    },
    methods: {
        validate: function () {
            if (!this.username) {
                this.validation.invalid.username = "Please enter your username.";
            } else {
                this.validation.invalid.username = "";
                this.validation.valid.username = "true";
            }
            if (!this.password) {
                this.validation.invalid.password = "Please enter password.";
            } else {
                this.validation.invalid.password = "";
                this.validation.valid.password = "true";
            }

            this.$forceUpdate();
            if (
                this.validation.valid.username == "true" &&
                this.validation.valid.password == "true"
            ) {
                this.$router.push("/listing");
            }
        },
    },
};
</script>

<style scoped>
* {
    margin: 0;
    padding: 0;
    font-family: "Varela Round", sans-serif
}

.box {
    background: linear-gradient(to right, #04619f, #000000);
    width: 100%;
    border: none;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

.form__field input {

    font-size: 14px;
    font-weight: 500;
    color: #000;
    border: none;
    background-color: #fff;
    margin: 5px;
    border-radius: 10px;
    padding: 15px;
    width: 300px;
    height: auto;
    box-sizing: border-box;
}

.form__field .btn {
    background-color: #48578a;
    font-size: 14px;
    color: #fff;
    width: 300px;
}

.btn-wrapper :hover {
    cursor: pointer;
}

span {
    color: #df2126e0;
}
</style>
