<template>
<div id="app" class="small-container">
    <div class="header-section">
        <div class="heading">
            <h1>Employee Details</h1>
            <router-link to="/">Logout</router-link>
        </div>
        <employee-form @add:employee="addEmployee" />
    </div>

    <employee-details v-bind:employees="employees" @edit:employee="editEmployee" @delete:employee="deleteEmployee" />
</div>
</template>

<script>
import EmployeeForm from '../components/EmployeeForm.vue'
import EmployeeDetails from '../components/EmployeeDetails.vue'

export default {
    name: 'ListingView',
    components: {
        EmployeeDetails,
        EmployeeForm,
    },
    data() {
        return {
            employees: [],
        }
    },
    methods: {
        async addEmployee(employee) {
            try {
                const response = await fetch('https://jsonplaceholder.typicode.com/users', {
                    method: 'POST',
                    body: JSON.stringify(employee),
                    headers: { "Content-type": "application/json; charset=UTF-8" }
                });
                const data = await response.json()
                this.employees = [...this.employees, data]
            } catch (error) {
                console.error('Error occured while adding employee: ' + error)
            }
        },

        async getEmployees() {
            try {
                const response = await fetch(`https://jsonplaceholder.typicode.com/users`)
                const data = await response.json()
                this.employees = data
            } catch (error) {
                console.error('Error occured while retrieving employees: ' + error);
            }
        },

        async editEmployee(id, updatedEmployee) {
            try {
                const response = await fetch(`https://jsonplaceholder.typicode.com/users/${id}`, {
                    method: 'PUT',
                    body: JSON.stringify(updatedEmployee),
                    headers: { "Content-type": "application/json; charset=UTF-8" }
                });

                const data = await response.json()
                this.employees = this.employees.map(employee => (employee.id === id ? data : employee))

            } catch (error) {
                console.error('Error while editing: ', +error)
            }
        },

        async deleteEmployee(id) {
            try {
                await fetch(`https://jsonplaceholder.typicode.com/users/${id}`, {
                    method: 'DELETE'
                });
                this.employees = this.employees.filter(employee => employee.id !== id);
            } catch (error) {
                console.error('Error while deleting: ', +error)
            }
        },
    },
    mounted() {
        this.getEmployees()
    }

}
</script>

<style>
#employee-details {
    width: 100%;
}

button {
    background: none;
    border: none;
    height: 24px;
}
.addemployee :hover {
  cursor: pointer;
}

input {
    height: 20px;
}

.small-container {

    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: left;
    color: #2c3e50;
    width: 100%;

}

.heading {
    color: #fff;
    border-radius: 3px 3px 0 0;
}

.heading a {
    position: absolute;
    right: 15px;
    top: 6px;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
}

.header-section {
    background: #45658dc9;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 20px;
    position: relative;

}

.heading h1 {
    font-size: 30px;
}

@media (max-width: 767px){
  .header-section {
    display: block;
  }
  .heading h1 {
    font-size: 25px;
}
.heading{
  margin-bottom: 5px;
}
#employee-form{
  display: flex;
  justify-content: center;
  
}

#employee-form form{
  width:100%;
}
#employee-form form input {
  width: 90%;
}

th , td {
  max-width:75px;
  overflow: hidden;
}

}

</style>
