<template>
    <div id="employee-details">
      <p v-if="employees.length < 1">
        No employees
      </p>
      <table v-else>
        <thead>
          <tr>
            <th>Name</th>
            <th>Email</th>
            <!-- <th>Phone</th>
            <th>Address</th> -->
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="employee in employees" v-bind:key="employee.id">
            <td v-if="editing === employee.id">
              <input type="text" v-model="employee.name" />
            </td>
            <td v-else>{{ employee.name }}</td>
  
            <td v-if="editing === employee.id">
              <input type="text" v-model="employee.email" />
            </td>
            <td v-else>{{ employee.email }}</td>

            <!-- <td v-if="editing === employee.id">
              <input type="text" v-model="employee.phone" />
            </td>
            <td v-else>{{ employee.phone }}</td>

            <td v-if="editing === employee.id">
              <input type="text" v-model="employee.address.city" />
            </td>
            <td v-else>{{ employee.address.city }}</td> -->
  
            <td v-if="editing === employee.id">
              
              <img @click="editEmployee(employee)" width="18" height="18" src="https://img.icons8.com/color/48/save--v1.png" alt="save--v1"/>
              <img @click="cancelEdit(employee)" class="muted-button" width="18" height="18" src="https://img.icons8.com/tiny-color/16/cancel.png" alt="cancel"/>
              
            </td>
  
            <td v-else>
              <img class="edit" width="20" height="20" src="https://img.icons8.com/color/48/edit--v1.png" alt="edit--v1" @click="editMode(employee)">
              <img class="delete" @click="$emit('delete:employee', employee.id)" width="20" height="20" src="https://img.icons8.com/color/48/delete-forever.png" alt="delete-forever"/>
              
              
            </td>
  
          </tr>
        </tbody>
      </table>
    </div>
  </template>
  
  <script>
    export default {
      name: 'employee-details',
      props: {
        employees: Array,
      },
  
      data() {
        return {
          editing: null,
        }
      },
  
      methods: {
        editMode(employee) {
          this.cachedEmployee = Object.assign({}, employee)
          this.editing = employee.id
        },
  
        cancelEdit(employee) {
          Object.assign(employee, this.cachedEmployee)
          this.editing = null
        },
  
        editEmployee(employee){
          if(employee.name === '' || employee.email === '') return
          this.$emit('edit:employee', employee.id, employee)
          this.editing = null        
        },
  
      }
    }
  </script>
  
  <style scoped>
  img:hover {
    cursor: pointer;
  }
  table {
    width: 100%;
    margin: auto;
    display: table;
    border-collapse: collapse;
    box-sizing: border-box;
    text-indent: initial;
    border-spacing: 2px;
    border-color: gray;
    color: #212529;
  }
  tr:hover {
    background-color:#f5f5f5;
  }
  thead {
    display: table-header-group;
    vertical-align: middle;
    height: 45px;
   
  }
  thead tr{
    background-color: #5f72a04a;
  }
  thead tr:hover {
    background-color: #5f72a04a  ;
  }
  /* button {
    margin: 0 0.5rem 0 0;
  } */
  
  input {
    margin: 0;
  }
  
  .empty-table {
    text-align: center;
  }


  th, td {
  padding: 8px;
  text-align: center;
  border-bottom: 1px solid #ddd;
}
.delete {
  margin-left: 12px;
}
.muted-button{
  margin-left: 12px;
}
table input {
  border-radius: 5px;
    border: 1px solid;
    text-align: center;
    height: 24px;
}

  </style>